/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import BodyClassName from "react-body-classname"
import { StaticQuery, graphql } from "gatsby"
import Helmet from "react-helmet"
import "./reset.css"
import Header from "../components/header/header"
import Styles from "./layout.module.css"
import FooterStyles from "../css/components/footer.module.css"

const Layout = ({ children }) => (
    <StaticQuery
        query={graphql`
            query SiteTitleQuery {
                site {
                    siteMetadata {
                        title
                    }
                }
            }
        `}
        render={data => (
            <BodyClassName className={`${Styles.layoutBody} theme--light`}>
                <>
                    <Helmet>
                        <title>James Maclennan - Web Developer</title>
                        <link
                            href="https://fonts.googleapis.com/css?family=Roboto|Lato"
                            rel="stylesheet"
                        />
                    </Helmet>
                    <Header
                        name={"James Maclennan"}
                        title={"Front End Web Developer"}
                    />
                    <main>{children}</main>
                    <footer className={FooterStyles.footer}>
                        <div className="container">
                            <div className={FooterStyles.wrapper}>
                                <div className={FooterStyles.contact}>
                                    James Maclennan
                                    <br />
                                    Vancouver, BC
                                    <br />
                                    <a href="tel:7789281262">(778) 928-1262</a>
                                    <br />
                                </div>
                                <div className={FooterStyles.copyright}>
                                    <span>
                                         {new Date().getFullYear()}, Built with
                                        {` `}
                                        <a href="https://www.gatsbyjs.org">
                                            GatsbyJS
                                        </a>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </footer>
                </>
            </BodyClassName>
        )}
    />
)

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

export default Layout
